import useGetPythLastPrice from '@/hooks/unstable-vault/useGetPythLastPrice'
import vaultInterface from '@/hooks/vault-interface'
import { useCalculateChangeLpRate, useCalculateSlippageAmount } from '@/hooks/vaults/useVaultsHelper'
import useGlobalStore from '@/store/common/global'
import useVaultsAddStore from '@/store/unstable-vaults/useVaultsAdd'
import useVaultsRiskStore from '@/store/vaults/useVaultsRisk'
import { VaultApiInfo } from '@/types'
import { Block, TradeInputGroup } from '@cetus/design'
import { useInterval } from '@cetus/hooks'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Box, Button, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import { v4 } from 'uuid'
import AvailableCapacity from '../detail/AvailableCapacity'
import { VaultsAddResult } from '../detail/VaultsAddResult'
import RiskModal from '../risk-modal'

type VaultDepositModeProps = {
  isUnstableVault: boolean
  displayTokenA?: Token | undefined
  displayTokenB?: Token | undefined
  vaultId: string
  isReverse: boolean
  assetAction: string
  isCheckedZAP: boolean
  currentActionTab: string
  autoRefresh: () => void
  apiVaultInfo: VaultApiInfo
  quoteCoin?: Token
  maxCapNum?: string
  hardCapUSD: string
  vaultTvl: string
  depositRatio: string
  calculateVaultTvlLoading: boolean
  contractVault: any
  availableCapacityWithQuoteCoin: string
  availableCapacityWithBaseCoin: string
  gtAumLimit: boolean
  lpTokenInfo: any
}
export default function VaultDepositMode(props: VaultDepositModeProps) {
  const {
    isUnstableVault,
    displayTokenA,
    displayTokenB,
    vaultId,
    isReverse,
    assetAction,
    isCheckedZAP,
    autoRefresh,
    apiVaultInfo,
    quoteCoin,
    baseCoin,
    maxCapNum,
    availableCapacityWithQuoteCoin,
    availableCapacityWithBaseCoin,
    hardCapUSD,
    vaultTvl,
    depositRatio,
    calculateVaultTvlLoading,
    contractVault,
    gtAumLimit,
    lpTokenInfo
  } = props
  console.log('ðŸš€ðŸš€ðŸš€ ~ DepositMode.tsx:66 ~ VaultDepositMode ~ lpTokenInfo:', lpTokenInfo)
  const useVaultsAdd = vaultInterface.useVaultsAdd(isUnstableVault)
  const useAddButtonStatus = vaultInterface.useAddButtonStatus(isUnstableVault)
  const { currentAccount, onWalletModal } = useAccountStore()
  const isQuoteCoin = useMemo(() => {
    return assetAction == quoteCoin?.coin_type
  }, [assetAction, quoteCoin])

  // åˆå§‹åŒ–add hookæ—¶åŒºåˆ† ä¼ å…¥tokenA,BæŒ‰ç…§åˆçº¦æ–¹å‘
  const {
    amountInputA,
    amountInputB,
    balanceInfoA,
    balanceInfoB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    fixAmountA,
    inputTotalValue,
    handleAmountInputChange,
    calculateResult,
    showOnlySideTips,
    doAddAction,
    overHardtop,
    zapNumError,
    calculateLpLoading,
    inputTotalValueError,
    percentage,
    setPercentage,
    setIsSlider,
    debouncedPreCalculateDepositValue,
    isSlider,
    setUuid
  } = useVaultsAdd(
    assetAction,
    autoRefresh,
    vaultId,
    isReverse ? displayTokenB : displayTokenA,
    isReverse ? displayTokenA : displayTokenB,
    isReverse,
    isCheckedZAP,
    hardCapUSD,
    vaultTvl,
    gtAumLimit,
    availableCapacityWithQuoteCoin,
    availableCapacityWithBaseCoin,
    quoteCoin,
    baseCoin,
    isQuoteCoin
  )

  const { btnText, btnDisabled } = useAddButtonStatus(
    isCheckedZAP,
    assetAction,
    true,
    amountInputA,
    amountInputB,
    fixAmountA,
    balanceInfoA?.balanceFormat,
    balanceInfoB?.balanceFormat,
    displayTokenA,
    displayTokenB,
    zapNumError,
    calculateResult,
    overHardtop,
    inputTotalValueError
  )

  const { vaultsSlippage } = useGlobalStore()

  // æœ€å°å¯æŽ¥æ”¶
  const { amountLimit } = useCalculateSlippageAmount(Number(vaultsSlippage), calculateResult?.ft_amount, false)
  // å æ¯” éžç¨³ lp_token_treasury ç¨³å®štotal_supply åˆ†åˆ«è¡¨ç¤ºæ± å­æ€»lp_tokenæ•°é‡
  const { lpRate } = useCalculateChangeLpRate(
    calculateResult?.ft_amount,
    isUnstableVault ? contractVault?.lp_token_treasury : contractVault?.total_supply
  )

  const { isApp } = useWindowWidth()

  const { pythPriceMap } = useVaultsAddStore()

  const priceMap = useMemo(() => {
    if (pythPriceMap) {
      return pythPriceMap
    }
  }, [pythPriceMap])

  const { getPythLastPrice } = useGetPythLastPrice()

  useEffect(() => {
    if (isSlider) {
      const uuid = v4()
      setUuid(uuid)
      debouncedPreCalculateDepositValue(
        percentage,
        uuid,
        isQuoteCoin ? availableCapacityWithQuoteCoin : availableCapacityWithBaseCoin,
        isQuoteCoin ? quoteCoin : baseCoin
      )
    }
  }, [priceMap])

  useInterval({
    interval: 20 * 1000,
    callback: () => {
      getPythLastPrice([fixCoinType(apiVaultInfo.tokenA.coin_type), fixCoinType(apiVaultInfo.tokenB.coin_type)])
    }
  })

  const hasFetchedPrice = useRef(false)

  useEffect(() => {
    if (!hasFetchedPrice.current && apiVaultInfo?.tokenA?.coin_type && apiVaultInfo?.tokenB?.coin_type) {
      getPythLastPrice([fixCoinType(apiVaultInfo.tokenA.coin_type), fixCoinType(apiVaultInfo.tokenB.coin_type)])
      hasFetchedPrice.current = true
    }
  }, [apiVaultInfo?.tokenA?.coin_type, apiVaultInfo?.tokenB?.coin_type])

  const { isCheckedAcceptWalletObj, isCheckedDontRemindWalletObj } = useVaultsRiskStore()
  const [isOpenRiskModal, setIsOpenRiskModal] = useState(false)
  const isShowRiskModal = useMemo(() => {
    console.log(
      'ðŸš€ðŸš€ðŸš€ ~ VaultsAdd.tsx:211 ~ isShowRiskModal ~ isCheckedDontRemindWalletObj[currentAccount?.address]:',
      isCheckedDontRemindWalletObj[currentAccount?.address]
    )
    return !isCheckedDontRemindWalletObj[currentAccount?.address]
  }, [isCheckedDontRemindWalletObj, currentAccount?.address])

  return (
    <>
      <Box w="100%" position="relative">
        <TradeInputGroup
          onClick={() => {}}
          from={{
            wrapStyle: { h: '108px' },
            balance: balanceInfoA?.balanceFormat || '',
            value: amountInputA,
            amountValue: amountInputA && amountValueA,
            loading: (isReverse ? fixAmountA : !fixAmountA) && preCalculateLoading && !isCheckedZAP,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenA?.coin_type,
            onChange: value => {
              if (apiVaultInfo?.category == 'haedal') {
                setIsSlider(false)
              }
              handleAmountInputChange(value, !isReverse, true, amountInputB)
            },
            selectable: false,
            placeholder: '0.0',
            token: displayTokenA,
            onFocusChange: (focus: boolean) => {}
          }}
          to={{
            wrapStyle: { h: '108px' },
            balance: balanceInfoB?.balanceFormat || '',
            value: amountInputB,
            amountValue: amountInputB && amountValueB,
            loading: (isReverse ? !fixAmountA : fixAmountA) && preCalculateLoading && !isCheckedZAP,
            onFocusChange: (focus: boolean) => {},
            onChange: value => {
              if (apiVaultInfo?.category == 'haedal') {
                setIsSlider(false)
              }
              handleAmountInputChange(value, isReverse, true, amountInputA)
            },
            selectable: false,
            hideSelf: assetAction !== 'both' && assetAction !== displayTokenB?.coin_type,
            placeholder: '0.0',
            token: displayTokenB
          }}
          iconParams={{
            xlinkHref: '#icon-icon_add',
            svgFill: 'text_caption',
            iconCursor: 'default'
          }}
          iconHover={false}
        />
      </Box>

      {zapNumError && percentage > 0 && (amountInputA || amountInputB) ? (
        <VStack w="100%" pb={calculateResult || showOnlySideTips ? '0px' : '20px'} alignItems="start" gap="20px">
          <Text
            color="primary_yellow"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_yellow_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            Single-asset deposit can't be higher than $10,000 at a time.
          </Text>
        </VStack>
      ) : null}

      <Button
        width="100%"
        height="52px"
        borderRadius="12px"
        marginBottom="8px"
        fontWeight="500"
        fontSize={zapNumError ? (isApp ? '12px' : '15px') : '16px'}
        isLoading={preCalculateLoading}
        disabled={btnDisabled || preCalculateLoading || (currentAccount?.address && (gtAumLimit || overHardtop))}
        onClick={() => {
          if (!currentAccount?.address) {
            onWalletModal(true)
          } else {
            if (isShowRiskModal) {
              setIsOpenRiskModal(true)
            } else {
              doAddAction()
            }
          }
        }}
      >
        {btnText || (isCheckedZAP ? 'Zap In' : 'Deposit')}
      </Button>

      {apiVaultInfo?.category == 'haedal' && (
        <Block bg="bg_six" borderColor="border_secondary" borderRadius="12px" p="16px" mb="8px">
          <AvailableCapacity
            depositRatio={depositRatio}
            percentage={percentage}
            hardCapUSD={hardCapUSD}
            vaultTvl={vaultTvl}
            calculateVaultTvlLoading={calculateVaultTvlLoading}
            quoteCoin={quoteCoin}
            maxCapNum={maxCapNum}
            gtAumLimit={gtAumLimit}
            availableCapacityWithQuoteCoin={availableCapacityWithQuoteCoin}
            key={vaultId}
            onChange={value => {
              setIsSlider(true)
              setPercentage(value)
              const uuid = v4()
              setUuid(uuid)
              debouncedPreCalculateDepositValue(
                value,
                uuid,
                isQuoteCoin ? availableCapacityWithQuoteCoin : availableCapacityWithBaseCoin,
                isQuoteCoin ? quoteCoin : baseCoin
              )
            }}
          />
        </Block>
      )}

      {inputTotalValueError && (
        <VStack w="100%" alignItems="start">
          <Text
            color="primary_red"
            fontSize="12px"
            textAlign="left"
            w="100%"
            bg="primary_red_opacity.10"
            p="12px"
            borderRadius="8px"
            lineHeight="20px"
          >
            The input is too small. Zap mode is not available.
          </Text>
        </VStack>
      )}

      {/* å±•ç¤ºè®¡ç®—ç»“æžœ */}
      {(calculateResult || showOnlySideTips) && !inputTotalValueError && (amountInputA || amountInputB) && (
        <VStack w="100%" pb="20px" alignItems="start" gap="0">
          {assetAction !== 'both' && showOnlySideTips && (
            <Text color="text_paragraph" fontSize="12px" lineHeight="1.8" pb={calculateResult ? '20px' : '0'}>
              {showOnlySideTips}
            </Text>
          )}
          {calculateResult && (
            <VaultsAddResult
              amountLimit={amountLimit}
              inputTotalValue={inputTotalValue}
              preCalculateLoading={preCalculateLoading}
              calculateLpLoading={calculateLpLoading}
              lpRate={lpRate}
              lpDecimals={lpTokenInfo?.decimals}
              poolName={`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
              labelColor="primary_gray"
            />
          )}
        </VStack>
      )}

      {isOpenRiskModal && (
        <RiskModal
          isOpen={isOpenRiskModal}
          setIsOpen={setIsOpenRiskModal}
          continueFunc={() => {
            setIsOpenRiskModal(false)
            doAddAction()
          }}
        />
      )}
    </>
  )
}
